.play {
  display: flex;
  height: 100%;
  width: 100%;
  top: 0;
  bottom:0;
  overflow: hidden;
  position: absolute;
  &-game {
    position: relative;
    display: flex;
    justify-content: flex-end;
    // align-items: center;
    flex-grow: 2;
    width: 70%;
    transition: all .5s ease;
    &.active {
      width: 100%;
    }
    .game {
      background-color: rgba(black,0.2);
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 0;
    }
  }
}
.touchBar {
  position: relative;
  z-index: 100;
  border-radius: 100px;
  display: flex;
  flex-wrap: wrap;
  width: 4rem;
  height: 4rem;
  margin-top: 2.5rem;
  margin-right: 10px;
  background:white;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
  transition: height .3s, opacity .3s ease;
  .handle {
    width:4rem;
    height:4rem;
    background: rgba($warning, .2) url(../images/icon/icon-toggle.svg) center no-repeat;
    background-size: auto 60%;
    border-radius: 100px;
  }
  &.is-pointer-down {
    opacity: .75;
  }
  &.on {
    height: 20rem;
    a {
      display: block;
      opacity: 1;
    }
  }
  a {
    display: none;
    opacity: 0;
    color: $secondary;
    border-radius: 100px;
    width: 4rem;
    height: 4rem;
    line-height: 4rem;
    font-size: 1.7rem;
    transition: all .3s;
    z-index: 1;
    &:hover {
      background-color: rgba($primary, .2);
      text-decoration: none;
    }
  }
  @media (orientation: landscape) {
    flex-direction: row-reverse;
    transition: width .3s, opacity .3s ease;
    &.on {
      height: 4rem;
      width: 20rem;
    }
   }
  // @include media-breakpoint-up(lg) {
  //   display: none;
  // }
}