html,
body {
  font-size: 13px;
  // font-family: 'Microsoft JhengHei', sans-serif;
  background-color: red($color: #000000);
  @include media-breakpoint-up(md) {
    font-size: 14px;
  }
  @include media-breakpoint-up(xl) {
    font-size: 16px;
  }
}
/* top nav */
.header {
  // position: relative;
}
.navbar {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  background-color: $light;
}
.nav-top {
  margin: 0;
  flex-wrap: nowrap;
  overflow-x: auto;
  max-width: 100%;
}
.nav-item {
  margin-left: .5rem;
  &:first-child {
    margin-left: 0;
  }
}
.nav-link {
  color: $secondary;
  border-radius: .25rem;
  white-space: nowrap;
  &.active,&:hover {
    color: $primary;
    background-color: rgba($primary, 0.1);
  }
}
.nav-pills {
  flex-direction: row-reverse;
}
.icon-more {
  width: 1rem;
  height: 1rem;
  display: block;
  background: url(../images/noun-more.svg) center no-repeat;
  background-size: auto 100%;
}
/* 7-11 open point nav */
.nav-open {
  position: fixed;
  width: 100%;
  bottom: 0;
  text-align: center;
  z-index: 100;
  background-image:linear-gradient(#ffffff00 50%, #f5f5f5 50%);
  >div {
    float: left;
    width: 20%;
    text-align: center;
    // max-height: 3rem;
  }
  img {
    max-width: 5rem;
    height: auto;
    vertical-align: middle;
  }
}
/*

*/
.owl-carousel {
  margin: 1rem 0;
  img {
    border-radius: 1rem;
  }
}
/* game app */
.owl-app {
  .item {
    width: 250px;
    @include media-breakpoint-up(md) {
      width: auto;
    }
  }
}
/* ebook */ 
.owl-book {
  .item {
    padding-right: 2rem;
    @include media-breakpoint-up(md) {
      padding-right: 0;
    }
  }
}
/* nav styles */
.owl-theme {
  position: relative;
	.owl-nav {
		>[class*='owl-'] {
      padding: 0;
      background-color: $light;
      width: 3rem;
      height: 3rem;
      box-shadow: 0 3px 5px 0px rgba($body-color, 0.2);
      border-radius: 100px;
      position: absolute;
      top: calc(50% - 1.5rem);
      overflow: hidden;
      // z-index: 10;
      span {
        position: absolute;
        font-size: 2.5rem;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -55%);
      }
			&:hover {
        color: $light;
				background-color:$primary;
			}
		}
    .owl-prev {
      left: 0;
      transform: translateX(-30%);
    }
    .owl-next {
      right: 0;
      transform: translateX(30%);
    }
		.disabled {
			opacity: 0;
		}
	}
}
.app {
  display: block;
  padding: .5rem;
  text-decoration: none;
  color: $body-color;
  border-radius: 1rem;
  transition: background-color, .3s;
  &:hover {
    color: $body-color;
    background-color: rgba($primary, 0.1);
    // background-color: $red-100;
  }
  &-intro {
    margin-top: 1rem;
    display: flex;
    >* {
      margin-right: 1rem;
    }
  }
  &-icon {
    width: calc(25% - 1rem);
    min-width: 56px;
    max-width: 128px;
    img {
      border-radius: 25%;
      overflow: hidden;
      max-width: 100%;
      height: auto;
    }
  }
  &-text {
    width: calc(75% - 1rem);
    p {
      white-space: nowrap;
      overflow: hidden;
      &:first-child {
        text-overflow: ellipsis;
      }
      &:nth-child(2) {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          width: 20%;
          height: 100%;
          right: 0;
          top: 0;
          // backdrop-filter: opacity(0.5);
          background-image: linear-gradient(to right, rgba(white,0),rgba(white,1))
        }
        @include media-breakpoint-up(md) {
          &::after {
            background-image: none;
          }
        }
      }
    }
    >* {
      margin-bottom: 0.25rem;
    }
  }
}
.badge {
  font-weight: normal;
  margin-right: 0.25rem;
  margin-bottom: 0.25rem;
  &:nth-child(odd) {
    background-color: $primary;
  }
  &:nth-child(even) {
    background-color: $secondary;
  }
}
.banner {
  margin-top: 2.5rem;
  display: block;
  img {
    border-radius: 1rem;
  }
}
.book-intro {
  margin-top: 1rem;
  >* {
    margin-bottom: .25rem;
  }
  >a {
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 100px;
    &:hover {
      color: white;
    }
  }
  >p {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/**
*
**/
.record-list {
 list-style: none;
 padding-left: 0;
  >li {
  padding: 1.5rem 1rem 0.5rem 1rem;
  margin-top: 1rem;
  background-color: $light;
  border-radius: 0.5rem;
  &:last-child {
    margin-bottom: 5rem;
  }
}
  p {
  small {
    color: $secondary;
  }
  &:first-child {
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  &:last-child {
    margin-bottom: 1rem;
  }
  margin-bottom: .25rem;
  // &:nth-child(n+1) {
  // }
}
}
.icon {
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center;
  &-toggle {
    background-image: url(../images/icon/icon-toggle.svg);
    background-size: 50% auto;
  }
  &-1 {
    background-image: url(../images/icon/icon-1.svg);
    background-size: 50% auto;
  }
  &-2 {
    background-image: url(../images/icon/icon-2.svg);
    background-size: 50% auto;
  }
  &-3 {
    background-image: url(../images/icon/icon-3.svg);
    background-size: 50% auto;
  }
  &-4 {
    background-size: auto 50%;
    background-image: url(../images/icon/icon-4.svg);
  }
  &-record {
    width: 1rem;
    height: 1rem;
    background-size: auto 100%;
    background-image: url(../images/icon/icon-record.svg);
  }
  &-headset {
    width: 1rem;
    height: 1rem;
    background-image: url(../images/icon/headset-solid.svg);
  }
  &-point {
    width: 2rem;
    height: 2rem;
    margin-right: 0.25rem;
    background-size: 100% auto;
    background-image: url(../images/icon/icon-3.svg);
  }
  &-point-used {
    width: 2rem;
    height: 2rem;
    margin-right: 0.25rem;
    background-size: 100% auto;
    background-image: url(../images/icon/icon-3.svg);
    filter: grayscale(1);
  }
}
.btn-task {
  width: 5rem;
  height: 5rem;
  border-radius: 100px;
  color: $light;
  border: 1px solid $primary;
  line-height: 4rem;
  position: relative;
  z-index: 1;
  &::after {
    content: '';
    position: absolute; 
    width: 90%;
    height: 90%;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 100px;
    background-color: rgba($primary, 1);
    z-index: -1;
  }
  &:hover {
    color: $light;
    font-weight: normal;
    filter: brightness(1.1);
  }
  &-over {
    border: 1px solid $secondary;
    &::after {
      background-color: rgba($secondary, 1);
    }
  }
  &-pre {
    border: 1px solid $warning;
    &::after {
      background-color: rgba($warning, 1);
    }
  }
}
.nav-task {
  width: 100%;
  display: flex;
  a {
    flex: 1 1 auto;
    color: $light;
    // background-color: $primary;
    &:nth-child(n+2) {
      margin-left: .5rem;
      // background-color: $warning;
    }
    &:hover {
      color: $light;
      // color: rgba($light, 0.75);
      // filter: brightness(1.2);
    }
  }
}
.label-img {
  width: auto;
  height: 3rem;
  margin-top: -1rem;
}

/**
*footer
**/
.footer {
  padding-bottom: .5rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
  small {
    color: $secondary;
  }
}

/**
*iframe
**/
.iframe {
  position: absolute;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  >iframe {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }
}