$fortune: #897fe0;
$fortune-gradient: #897fe0 0%, #89b0eb 100%;

.fortune-title {
  margin-top: 1rem;
  padding: .75rem 1rem;
  color: $light;
  font-size: 1.5rem;
  background-image: linear-gradient(to right, $fortune-gradient);
  border-radius: .5rem;
  display: flex;
  align-items: center;
}

.badge-fortune.badge-fortune {
  color: $light;
  background-color: $fortune;
}

.fortune {
  &-list {
    // background-color: $light;
    text-decoration: none;
    display: block;
    max-width: 9rem;
    height: 100%;
    margin-bottom: 1rem;
    margin-left: auto;
    margin-right: auto;

    img {
      width: 100%;
      height: 12rem;
      border-radius: .5rem;
      object-fit: cover;
      object-position: center center;
      margin-bottom: .5rem;
      box-shadow: 0px 0px 2px $secondary;
    }

    p {
      margin-bottom: 0rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      color: $body-color;
    }

    small {
      color: $secondary;
    }

    // @include media-breakpoint-up(md) {
    //   width: 9rem;
    // }
  }

  &-teller {
    margin-top: 1.5rem;
    padding-left: .5rem;
    padding-right: .5rem;

    >* {
      margin-bottom: 1rem;
    }

    img {
      max-width: 9rem;
      width: 100%;
      height: 12rem;
      border-radius: .5rem;
      object-fit: cover;
      object-position: center center;
      box-shadow: 0px 0px 2px $secondary;
    }

    h5 {
      line-height: 2rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }

    li {
      margin-bottom: .5rem;
    }

    span {
      color: $secondary;
    }

    .price {
      display: flex;
      align-items: center;
      text-align: center;
      background-color: $light;
      // background-color: rgba($yellow, .15);
      padding: 1rem;
      border-radius: .5rem;

      >* {
        flex: 1;
        padding: .5rem;
      }
    }
  }
  &-form {
    padding: 1.5rem;
    .form-control {
      &:focus {
        border-color: rgba($fortune, .5);
        box-shadow: 0 0 0 0.25rem rgba($fortune, 0.25);
      }
    }
    .form-select {
      display: inline-block;
      width: auto;
      &:focus {
        border-color: rgba($fortune, .5);
        box-shadow: 0 0 0 0.25rem rgba($fortune, 0.25);
      }
    }
    .form-check {
      display: inline-block;
      &-input {
        &:checked {
          background-color: $fortune;
          border-color: $fortune;
        }
        &:focus {
          border-color: rgba($fortune, .5);
          box-shadow: 0 0 0 0.25rem rgba($fortune, 0.25);
        }
      }
    }
    .col-form-label {
      margin-top: 1.5rem;
      @include media-breakpoint-up(sm) {
        margin-top: .5rem;
      }
    }
  }
  &-form-result {
    ul {
      padding: 1.5rem;
      line-height: 2rem;
    }
    li {
      // @include list-unstyled;
      margin-bottom: 1rem;
    }
    span {
      margin-right: 0.5rem;
    }
  }
}

.process-list {
  li {
    background-color: rgba($fortune, .25);
    padding: 2rem 1rem;
    border-radius: .5rem;
    text-align: center;
    margin-bottom: 1.5rem;
    position: relative;
    &::after {
      position: absolute;
      width: 0;
      height: 0;
      margin: auto;
      bottom: -1rem;
      left: 0;
      right: 0;
      content: '';
      border: transparent 10px solid;
      border-top-color: $fortune;
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
  }
}

.btn-fortune {
  color: $light;
  &:hover {
    color: $light;
  }
}

.nav-pills-fortune {
  flex-direction: row-reverse;
  .nav-link {
    background: none;
    border: 0;
    @include border-radius($nav-pills-border-radius);
    &:hover {
      color: $fortune;
    } 
  }

  .nav-link.active,
  .show > .nav-link {
    color: $nav-pills-link-active-color;
    background-color: $fortune;
  }
}

.tarot {
  max-width: 640px;
  margin-right: auto;
  margin-left: auto;
  background-color: #512281;
  a {
    &:hover {
      transform: scale(1.05);
      filter: brightness(1.1);
    }
  }
}

.btn-tarot {
  font-size: 1.75rem;
  color: #512281;
  font-weight: bold;
  width: 90%;
  max-width: 550px;
  margin: auto;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-image: linear-gradient(to bottom, $yellow 0% ,$warning 100%);
  background-color: $yellow;
  transition: all .3s;
  &-position {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 1.5rem;
  }
}
