.calendar,
.calendar-info {
  // max-width: 64rem;
  margin: 1rem auto;
  padding: 0.5rem;
  border-radius: .5rem;
  overflow: hidden;
  background-image: linear-gradient(180deg, #ede2ff 0%, #c8ecff 100%);
}

.calendar-header {
  display: flex;
  padding: 1rem .5rem;
  align-items: center;
  >* {
    margin-right: .5rem;

    &:last-child {
      margin-right: 0;
      margin-left: auto;
    }
  }

  img {
    width: 20%;
    padding: .5rem;
    border-radius: .5rem;
  }
}

.weekdays,
.day-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: .5rem;
  margin: 0 auto;
  padding: 0;

  li {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.weekdays {
  margin-bottom: 1rem;

  li {
    height: 4vw;
  }

  abbr[title] {
    border: none;
    font-weight: 800;
    text-decoration: none;
  }
}

.day-grid {
  li {
    background-color: $light;
    //  height: 12vw;
    padding-top: 2vw;
    padding-bottom: 2vw;
    max-height: 125px;
    border-radius: .5rem;
    text-align: center;
    cursor: pointer;

    &.selected {
      background-image: linear-gradient(to top, #897fe0 0%, #89b0eb 100%);
      color: $light;
      span,small {
        color: $light;
      }
    }

    &.month-other {
      opacity: .5;
    }

    * {
      width: 100%;
    }

    small {
      transform: translateY(-10%);
    }

    &:nth-child(7n),
    &:nth-child(7n+1) {
      color: #e67373;
    }
  }

}

@media all and (max-width: 800px) {

  .weekdays,
  .day-grid {
    grid-gap: .25rem;
  }

  .weekdays li {
    font-size: 0;
  }

  .weekdays>li abbr:after {
    content: attr(title);
    font-weight: bold;
    font-size: calc(1rem + (26 - 16) * ((100vw - 300px) / (1600 - 300)));
    text-align: center;
  }
}

.calendar-title {
  margin-top: 1rem;
  padding: .75rem 1rem;
  color: $light;
  font-size: 1.5rem;
  background-image: linear-gradient(to right, #897fe0 0%, #89b0eb 100%);
  border-radius: .5rem;
}

.calendar-info {
  margin-bottom: 5rem;

  header {
    padding: .75rem 1rem;
    color: $light;
    font-size: 1.5rem;
    background-image: linear-gradient(to left, #897fe0 0%, #89b0eb 100%);
    display: flex;
    border-radius: .5rem;
    margin-bottom: 1rem;

    span {
      &:nth-child(2) {
        margin-left: auto;
      }
    }
  }
}

.calendar-content {
  text-align: center;
  // padding-left: 1rem;
  // padding-right: 1rem;

  p {
    margin-bottom: 0.5rem;

    &:nth-child(1) {
      font-size: 3rem;
      font-weight: bolder;
    }
  }

  &-text {
    display: flex;
    padding: 1rem;
    align-items: center;
    text-align: left;

    p {
      padding: 0.25rem 1rem;
      margin-right: 1rem;
      color: $light;
      border-radius: 1rem;

      &.yes {
        background-color: $danger;
      }

      &.no {
        background-color: $secondary;
      }
    }
  }
}